.benefits {
  counter-reset: section;
  position: relative;
  &__title {
    font-family: var(--main-font);
    font-style: normal;
    font-weight: 800;
    font-size: 20px;
    line-height: 1.35;
    letter-spacing: 0.03em;
    margin-bottom: 30px;
    width: 180px;
    height: 50px;
    color: var(--second-color);
    // @include mq(mobile) {
    //   width: 180px;
    //   height: 50px;
    // }
    @include mq(tablet) {
      width: 492px;
      height: 31px;
      margin-left: auto;
      margin-right: auto;
      text-align: center;
      font-size: 26px;
      line-height: 1.3;
    }
    @include mq(desktop) {
      position: absolute;
      white-space: nowrap;
      width: 1px;
      height: 1px;
      overflow: hidden;
      border: 0;
      padding: 0;
      clip: rect(0 0 0 0);
      clip-path: inset(50%);
      margin: -1px;
    }
  }
}
.benefits__container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -71%);

  @include mq(tablet) {
    transform: translate(-50%, -50%);
  }
}
.benefits-list {
  display: flex;
  flex-direction: column;
  box-shadow: 4px 6.928px 15.54px 5.46px rgb(29 29 27 / 4%);
  list-style: none;
  padding: 20px 14px 20px 30px;
  margin-left: auto;
  margin-right: auto;
  height: 638px;
  background-color: var(--second-color);

  @include mq(tablet) {
    justify-content: center;
    flex-wrap: wrap;
    flex-direction: row;
    gap: 20px;
    max-height: 394px;
  }
  @include mq(desktop) {
    max-width: 1314px;
    gap: 40px;
    padding: 70px 80px 70px;
  }
  &__item {
    margin-bottom: 36px;
    @include mq(tablet) {
      width: 230px;
      margin-bottom: 18px;
    }
    @include mq(desktop) {
      width: 356px;
      margin-bottom: 0;
    }
  }
}
.benefits-list-text {
  font-family: var(--main-font);
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.4;
  letter-spacing: 0.08em;
  @include mq(desktop) {
    font-size: 20px;
  }
}
.benefits-list-number {
  margin-bottom: 10px;
  @include mq(tablet) {
    margin-bottom: 18px;
  }
  @include mq(desktop) {
    margin-bottom: 10px;
  }
}
.benefits-list-number::before {
  counter-increment: section;
  content: '— 0' counter(section) ' ';
  font-weight: var(--bold-weight);
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 0.08em;
  color: var(--accent-color);
}
